	module.exports = {"data":{"challengeNode":{"title":"Problem 416: A frog's trip","description":["A row of n squares contains a frog in the leftmost square. By successive jumps the frog goes to the rightmost square and then back to the leftmost square. On the outward trip he jumps one, two or three squares to the right, and on the homeward trip he jumps to the left in a similar manner. He cannot jump outside the squares. He repeats the round-trip travel m times.","","Let F(m, n) be the number of the ways the frog can travel so that at most one square remains unvisited.","For example, F(1, 3) = 4, F(1, 4) = 15, F(1, 5) = 46, F(2, 3) = 16 and F(2, 100) mod 109 = 429619151.","","Find the last 9 digits of F(10, 1012)."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-416-a-frogs-trip","blockName":"Project Euler","tests":[{"text":"<code>euler416()</code> should return 898082747.","testString":"assert.strictEqual(euler416(), 898082747, '<code>euler416()</code> should return 898082747.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler416() {\n  // Good luck!\n  return true;\n}\n\neuler416();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-417-reciprocal-cycles-ii","id":"5900f50e1000cf542c510020"},"slug":"/coding-interview-prep/project-euler/problem-416-a-frogs-trip"}}

